import React from 'react';
import s from './Post.module.css';


const Post = (props) => {
 

  return (
    <div className={s.item}>
       <img src= {props.profile.profile?.photos?.small} />
     
      <h2>{props.message}</h2>
      <div>
        <span>Likes</span> {props.likesCount}
        <button onClick={ () => {props.incrementLikesCount(props.id)}}>Like</button>
        <button onClick={() => {props.decrementLikesCount(props.id)}}>Dislike</button>
        <button onClick={ () => {props.deletePost(props.id)} }>Delete</button>
      </div>
    </div>

  )
}

export default Post;