import React from 'react';
import s from './MyPosts.module.css';
import Post from './Post/Post';
import { Field } from 'redux-form';
import { reduxForm } from 'redux-form';
import { maxLengthCreator, reqired } from '../../../utils/validators/validators';
import { Textarea } from '../../common/FormsControls/FormsControls';
import MySelect from '../../common/MySelect';
import { useState } from 'react';
import { useMemo } from 'react';
import MyModal from './MyModal';
import { CSSTransition, TransitionGroup } from 'react-transition-group';
import './Transition.module.css'

const MyPosts = React.memo(props => {

  const [selectedSort, setSelectedSort] = useState('');
  const [searchQuery, setSearchQuery] = useState('');
  const [modal, setModal] = useState(false);

  const sortPosts = (sort) => {
    setSelectedSort(sort);
    props.sortPostsActionCreator(sort)
  }

  // const searchPost = (search) => { заготовка для использования в редьюсере
  //   setSearchQuery(search);
  //   props.searchPost(search)
  // }

  let onAddPost = (values) => {
    props.addPostActionCreator(values.newPostText);
    setModal(false);
  }

  let sortedAndSearchedPosts = useMemo(() => {
    return [...props.posts].filter(post => post.message.toLowerCase().includes(searchQuery.toLowerCase()))
  }, [searchQuery, selectedSort, props.posts])

  let postsElements = sortedAndSearchedPosts
    .map(p => 
    <CSSTransition key={p.id} timeout={500} className="post" >
    <Post
      id={p.id}
      message={p.message}
      likesCount={p.likesCount}
      incrementLikesCount={props.incrementLikesCount}
      decrementLikesCount={props.decrementLikesCount}
      profile={props.profile}
      deletePost={props.deletePost} />
      </CSSTransition>)

  return (
    <div className={s.postsBlock}>
      <h3>My posts </h3>

      <input value={searchQuery} onChange={e => setSearchQuery(e.target.value)} placeholder='Search...' />
      <div>
        <button onClick={() => setModal(true)}> Create new post </button>
        <MyModal visible={modal} setVisible={setModal} >
          <AddNewPostRedux onSubmit={onAddPost} newPostText={props.newPostText} />
        </MyModal>
        <MySelect
          value={selectedSort}
          onChange={sortPosts}
          defaultValue='Sorting'
          options={[
            { value: 'message', name: 'By message' },
            { value: 'likesCount', name: 'By likes count' }]}
        />
      </div>

      {sortedAndSearchedPosts.length
        ?
        <div className={s.posts}>
         <TransitionGroup component={null} >
          {postsElements}
          </TransitionGroup>
        </div>
        : <b>Posts not found...</b>}
    </div>
  )
})

const maxLength100 = maxLengthCreator(100);


const AddNewPost = (props) => {
  return (
    <form onSubmit={props.handleSubmit}>
      <div>
        <Field component={Textarea} name={'newPostText'} validate={[reqired, maxLength100]} placeholder={'Post message'} />
      </div>
      <div>
        <button>Add post</button>
      </div>
    </form>
  )
}
const AddNewPostRedux = reduxForm({ form: 'ProfileAddNewPostForm' })(AddNewPost)

export default MyPosts;